<template>
  <el-container>
    <el-header class="header">
      <div class="" >
        企业姓名：
        <el-input class="com-input"
          placeholder="请输入企业姓名"
          v-model="name">
        </el-input>
        企业类型：
        <el-select v-model="type" placeholder="全部">
          <el-option
            v-for="item in typeList"
            :key="item.id"
            :label="item.dValue"
            :value="item.dKey">
          </el-option>
        </el-select>
        <el-button type="primary" icon="el-icon-search" class="btn" @click="search">搜索</el-button>
        <el-button type="primary" @click="addCom" class="btn">新增企业</el-button>
      </div>
    </el-header>
    <el-main>
      <el-table :data="comInfoList">
        <el-table-column prop="id" label="ID">
        </el-table-column>
        <el-table-column prop="name" label="企业名字">
        </el-table-column>
        <el-table-column prop="address" label="企业地址">
        </el-table-column>
        <el-table-column prop="property" label="公司性质">
        </el-table-column>
        <el-table-column prop="type" label="企业类型">
        </el-table-column>
        <el-table-column prop="ipo" label="是否上市">
        </el-table-column>
        <el-table-column prop="mobile" label="联系电话">
        </el-table-column>
        <el-table-column prop="createTime" label="入驻时间">
        </el-table-column>
        <el-table-column label="操作" width='220'>
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="text" size="small">编辑</el-button>
            <el-button type="text" size="small" @click="goodsManageClick(scope.row)">商品管理</el-button>
            <el-button type="text" size="small" @click="serveClick">服务单管理</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="demonstration-box" v-show='pageShow'>
        <!-- <span class="demonstration">显示总数</span> -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="100"
          layout="total, prev, pager, next"
          :total="1000">
        </el-pagination>
      </div>
    </el-main>
  </el-container>
</template>
<script>
import getList from '../../utils/getList'
import queryList from '../../utils/queryList'
import {typeList} from '../../data/listBody'
export default {
  name: 'comList',
  data() {
    return {
      comInfoList: [],
      name: '',
      type: '',
      currentPage: 1,
      pageShow: false,
      typeList
    }
  },
  created() {
    this.search()
  },
  methods: {
    addCom() {
      this.$router.push('addOrEditCom?type=add')
    },
    serveClick() {
      this.$router.push('servesList')
    },
    handleClick(item) {
      this.$router.push('addOrEditCom?type=edit&id=' + item.id)
    },
    handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.currentPage = val;
    },
    goodsManageClick(item) {
      this.$router.push('../goodsManage/?name=' + item.name)
    },
    search() {
      const params = {
        name: this.name,
        type: this.type,
        page: this.currentPage -1,
        size: 20
      }
      queryList('company', params).then((res) => {
        this.comInfoList = res.data.data.list
        this.pageShow = this.comInfoList.length > 20
      }).catch((err) => {
        console.log(err)
        this.$message(err.data.message)
      })
    }
  }
}
</script>
<style lang="less" scoped>
  .header {
    text-align: left;
    border-bottom: 1px solid #eee
  }
  .com-input {
    width: 210px;
    margin-right: 20px
  }
  .btn{
    margin-left: 20px;
  }
</style>

